import React, { memo, useState, useEffect } from "react";
import {
  Modal,
  Form,
  Input,
  Select,
  Button,
  Space,
  Row,
  Col,
  message,
  InputNumber,
} from "antd";
import { Tree } from "antd";
import { MinusCircleOutlined, PlusOutlined } from "@ant-design/icons";
import {
  personal_adjust_cond_config_get_all,
  personal_adjust_rules_config_create,
  personal_adjust_rules_config_update,
} from "../../../api/room";
// import { role_create } from "../../../api/role";
const { Option } = Select;
const { TextArea } = Input;
const Index = (props) => {
  const { onCancel, visible, onOk, info } = props;
  const [form] = Form.useForm();
  const [trigger_conds_group, set_trigger_conds_group] = useState([null]);
  const [complete_conds_group, set_complete_conds_group] = useState([null]);
  const [adjust_arr, set_adjust_arr] = useState([]);
  //提交
  const submit = async () => {
    try {
      form.validateFields().then(async () => {
        await personal_adjust_rules_config_update({
          id: info.id,
          ...form.getFieldsValue(),
          trigger_conds_group: form
            .getFieldValue("trigger_conds_group")
            .map((item) => item.val)
            .toString(),
          complete_conds_group: form
            .getFieldValue("complete_conds_group")
            .map((item) => item.val)
            .toString(),
        });
        message.success("修改成功");
        onOk();
      });
    } catch (e) {
      console.error(e);
    }
  };
  //获取个人条件
  const personal_adjust_cond_config_get_all_h = async () => {
    let data = await personal_adjust_cond_config_get_all();
    data = data.data;
    const adjust_arr_m = data.map((item) => ({
      value: item.id,
      title: `${item.id}-${item.desc}`,
    }));
    set_adjust_arr(adjust_arr_m);
  };
  const FormConfig = {
    labelCol: { span: 8 },
    wrapperCol: { span: 16 },
  };

  const on_cancel = () => {
    onCancel();
  };

  const trans_val = (data) => {
    let arr = data.split(",");
    let new_arr = [];
    new_arr = arr.map((item, index) => ({
      val: item,
      fieldKey: index,
      isListField: true,
      key: index,
      name: index,
    }));
    return new_arr;
  };
  const reset_h = () => {
    form.resetFields();
  };
  useEffect(() => {
    if (visible) {
      form.setFieldsValue({
        ...info,
        trigger_conds_group: trans_val(info.trigger_conds_group),
        complete_conds_group: trans_val(info.complete_conds_group),
      });
      personal_adjust_cond_config_get_all_h();
    }
  }, [visible]);

  return (
    <Modal
      title="修改规则"
      visible={visible}
      onCancel={on_cancel}
      onOk={submit}
      afterClose={() => reset_h()}
    >
      <Form
        {...FormConfig}
        form={form}
        name="form_in_modal"
        hideRequiredMark={true}
      >
        <Form.Item
          label="标志"
          name="flag"
          rules={[{ required: true, message: "请选择标志" }]}
        >
          <Select placeholder="请选择标志">
            <Option value={0}>收分</Option>
            <Option value={1}>放分</Option>
          </Select>
        </Form.Item>
        <Form.List
          label="触发条件组合"
          name="trigger_conds_group"
          labelCol={FormConfig.labelCol.span}
          rules={[
            { required: true, message: "请选择触发条件" },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if (!value || value?.length === 0) {
                  message.error("请添加触发条件");
                  return Promise.reject(new Error("请添加触发条件"));
                }
                return Promise.resolve();
              },
            }),
          ]}
        >
          {(fields, { add, remove }) => (
            <>
              {fields.map(({ key, name, fieldKey, ...restField }) => (
                <div
                  key={key}
                  style={{
                    display: "flex",
                    marginBottom: 8,
                    alignItems: "center",
                  }}
                >
                  <Form.Item
                    {...restField}
                    label="触发条件"
                    name={[name, "val"]}
                    fieldKey={[fieldKey, "val"]}
                    rules={[{ required: true, message: "请输入触发条件" }]}
                  >
                    <Select
                      showSearch
                      placeholder="请选择触发条件"
                      suffixIcon={
                        <MinusCircleOutlined
                          style={{}}
                          onClick={() => remove(name)}
                        />
                      }
                    >
                      {adjust_arr.map((item) => (
                        <Option key={item.value} value={item.value}>
                          {item.title}
                        </Option>
                      ))}
                    </Select>
                  </Form.Item>
                </div>
              ))}
              <Form.Item
                wrapperCol={{
                  span: FormConfig.wrapperCol.span,
                  offset: FormConfig.labelCol.span,
                }}
              >
                <Button
                  type="dashed"
                  onClick={() => {
                    // add_trigger_h(add);
                    add();
                  }}
                  block
                  icon={<PlusOutlined />}
                >
                  添加触发条件
                </Button>
              </Form.Item>
            </>
          )}
        </Form.List>

        <Form.List
          label="中断条件组合"
          name="complete_conds_group"
          labelCol={FormConfig.labelCol.span}
          rules={[
            { required: true, message: "请输入中断条件" },
            ({ getFieldValue }) => ({
              validator(_, value) {
                if (!value || value?.length === 0) {
                  message.error("请添加中断条件");
                  return Promise.reject(new Error("请添加中断条件"));
                }
                return Promise.resolve();
              },
            }),
          ]}
        >
          {(fields, { add, remove }) => (
            <>
              {fields.map(({ key, name, fieldKey, ...restField }) => (
                <div
                  key={key}
                  style={{
                    display: "flex",
                    marginBottom: 8,
                    alignItems: "center",
                  }}
                >
                  <Form.Item
                    {...restField}
                    label="中断条件"
                    name={[name, "val"]}
                    fieldKey={[fieldKey, "val"]}
                    rules={[{ required: true, message: "请选择中断条件" }]}
                  >
                    <Select
                      showSearch
                      placeholder="请选择中断条件"
                      suffixIcon={
                        <MinusCircleOutlined
                          style={{}}
                          onClick={() => remove(name)}
                        />
                      }
                    >
                      {adjust_arr.map((item) => (
                        <Option key={item.value} value={item.value}>
                          {item.title}
                        </Option>
                      ))}
                    </Select>
                    {/* <MinusCircleOutlined
                      style={{ marginLeft: "8px" }}
                      onClick={() => remove(name)}
                    /> */}
                  </Form.Item>
                </div>
              ))}
              <Form.Item
                wrapperCol={{
                  span: FormConfig.wrapperCol.span,
                  offset: FormConfig.labelCol.span,
                }}
              >
                <Button
                  type="dashed"
                  onClick={() => {
                    add();
                  }}
                  block
                  icon={<PlusOutlined />}
                >
                  添加中断条件
                </Button>
              </Form.Item>
            </>
          )}
        </Form.List>

        <Form.Item
          label="强制中断次数"
          name="break_cnt"
          rules={[{ required: true, message: "请输入强制中断次数" }]}
        >
          <InputNumber placeholder="请输入强制中断次数" />
        </Form.Item>
        <Form.Item
          label="触发概率"
          name="trigger_fac"
          rules={[{ required: true, message: "请输入触发概率" }]}
        >
          <InputNumber placeholder="请输入触发概率" />
        </Form.Item>
        <Form.Item
          label="每次触发概率修正值"
          name="trigger_delta_fac"
          rules={[{ required: true, message: "请输入每次触发概率修正值" }]}
        >
          <InputNumber placeholder="请输入每次触发概率修正值" />
        </Form.Item>
        <Form.Item
          label="权重"
          name="weight"
          rules={[{ required: true, message: "请输入权重" }]}
        >
          <InputNumber placeholder="请输入权重" />
        </Form.Item>
        <Form.Item
          label="玩家配牌分数最小值"
          name="self_score_min"
          rules={[{ required: true, message: "请输入玩家配牌分数最小值" }]}
        >
          <InputNumber placeholder="请输入玩家配牌分数最小值" />
        </Form.Item>
        <Form.Item
          label="玩家配牌分数最大值"
          name="self_score_max"
          rules={[{ required: true, message: "请输入玩家配牌分数最大值" }]}
        >
          <InputNumber placeholder="请输入玩家配牌分数最大值" />
        </Form.Item>
        <Form.Item
          label="玩家配牌每次修正值"
          name="self_score_delta_fac"
          rules={[{ required: true, message: "请输入玩家配牌每次修正值" }]}
        >
          <InputNumber placeholder="请输入玩家配牌每次修正值" />
        </Form.Item>
        <Form.Item
          label="机器人配牌分数最小值"
          name="wingman_score_min"
          rules={[{ required: true, message: "请输入机器人配牌分数最小值" }]}
        >
          <InputNumber placeholder="请输入机器人配牌分数最小值" />
        </Form.Item>
        <Form.Item
          label="机器人配牌分数最大值"
          name="wingman_score_max"
          rules={[{ required: true, message: "请输入机器人配牌分数最大值" }]}
        >
          <InputNumber placeholder="请输入机器人配牌分数最大值" />
        </Form.Item>
        <Form.Item
          label="机器人配牌每次修正值"
          name="wingman_score_delta_fac"
          rules={[{ required: true, message: "请输入机器人配牌每次修正值" }]}
        >
          <InputNumber placeholder="请输入机器人配牌每次修正值" />
        </Form.Item>
        <Form.Item
          label="描述"
          name="desc"
          rules={[{ required: true, message: "请输入描述" }]}
        >
          <TextArea placeholder="请输入描述" />
        </Form.Item>
      </Form>
    </Modal>
  );
};
export default memo(Index);
