import { Table, Button, Popconfirm, message } from "antd";
import React, {
  useEffect,
  useState,
  forwardRef,
  useImperativeHandle,
} from "react";
import dayjs from "dayjs";
import {
  personal_adjust_rules_config_get,
  personal_adjust_rules_config_set_status,
} from "../../../api/room";
// import { personal_adjust_rules_config_get, get_by_user_nam } from "../../../api/acount";
// import mock from ''
const ListTable = (props, ref) => {
  useImperativeHandle(ref, () => ({
    // changeVal 就是暴露给父组件的方法
    getData: (newVal) => {
      personal_adjust_rules_config_get_handle(newVal);
    },
  }));
  const {
    modifypassword_h,
    modifyaccountRole_h,
    block_h,
    pageSize = 10,
    total = 0,
    update_h,
    showTotal = true,
  } = props;
  const [loading, setLoading] = useState(false);
  const [data, setData] = useState([]);
  const [pagination, setPagination] = useState({
    current: 1,
    pageSize: pageSize || 10,
    total: total || 0,
    showSizeChanger: true,
  });
  //标志转换
  const flag_trans_text = (type) => {
    let text;
    switch (type) {
      case 0:
        text = "收分";
        break;
      case 1:
        text = "放分";
        break;
    }
    return text;
  };
  const set_status_h = async (status, id) => {
    await personal_adjust_rules_config_set_status({
      status: status == 0 ? 1 : 0,
      id
    });
    personal_adjust_rules_config_get_handle();
    message.success("修改成功");
  };
  const columns = [
    { title: "规则id", dataIndex: "id", key: "id", fixed: "left", width: 100 },
    { title: "描述", dataIndex: "desc" },

    {
      title: "标志",
      dataIndex: "flag",
      render: (_, record) => <span>{flag_trans_text(record.flag)}</span>,
    },
    { title: "触发条件组", dataIndex: "trigger_conds_group" },
    {
      title: "中断条件组",
      dataIndex: "complete_conds_group",
    },

    { title: "强制中断次数", dataIndex: "break_cnt" },
    {
      title: "触发概率",
      dataIndex: "trigger_fac",
    },
    { title: "每次触发概率修正值", dataIndex: "trigger_delta_fac" },
    {
      title: "玩家配牌分数最小值",
      dataIndex: "self_score_min",
    },
    { title: "玩家配牌分数最大值", dataIndex: "self_score_max" },
    {
      title: "玩家配牌每次修正值",
      dataIndex: "self_score_delta_fac",
    },
    { title: "机器人配牌分数最小值", dataIndex: "wingman_score_min" },
    {
      title: "机器人配牌分数最大值",
      dataIndex: "wingman_score_max",
    },

    { title: "机器人配牌每次修正值", dataIndex: "wingman_score_delta_fac" },
    {
      title: "权重",
      dataIndex: "weight",
    },
    {
      title: "状态",
      dataIndex: "status",
      render: (_, record) => <span>{record.status == 0 ? "有效" : "失效"}</span>,
    },
    {
      title: "操作",
      dataIndex: "operation",
      align: "center",
      width: 200,
      fixed: "right",
      render: (text, record, index) => {
        return (
          <span>
            <Button
              type="link"
              onClick={() => {
                update_h(record);
              }}
            >
              编辑
            </Button>
            <Popconfirm
              title={`确定${record.status == 0 ? "关闭" : "启用"}这条规则？`}
              onConfirm={() => {
                set_status_h(record.status, record.id);
              }}
              okText="确定"
              cancelText="取消"
            >
              <Button type="link">
                {record.status == 0 ? "关闭" : "启用"}
              </Button>
            </Popconfirm>
          </span>
        );
      },
    },
  ];
  //获取列表
  const personal_adjust_rules_config_get_handle = async (params = {}) => {
    try {
      setLoading(true);
      let data = await personal_adjust_rules_config_get({
        page: pagination.current,
        page_size: pagination.pageSize,
        ...params,
      });
      // console.log(data)
      setData(data.data);
        setPagination({
        ...pagination,
        total: data.page.count,
      });
      // console.log(pagination);
      setLoading(false);
    } catch (e) {
      setLoading(false);
      console.error(e);
    }
  };
  //表格改变触发
  const onchange = (pagination) => {
    // console.log(pagination)
    setPagination({ ...pagination });
  };
  useEffect(() => {
    personal_adjust_rules_config_get_handle();
  }, [pagination.current, pagination.pageSize]);
  return (
    <>
      <Table
        bordered
        loading={loading}
        columns={columns}
        dataSource={data}
        rowKey="id"
        pagination={pagination}
        onChange={onchange}
        scroll={{ x: 3300 }}
      ></Table>
    </>
  );
};
export default forwardRef(ListTable);
